import React from "react";
import classes from "./Calculator.module.css";
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
class Calculator extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            displayValue: "0",
            formula: "",
            clearFormula: "",
            evaluated: false,
        };
        this.handleNumberClick = this.handleNumberClick.bind(this);
        this.handleOperatorClick = this.handleOperatorClick.bind(this);
        this.handleDecimalClick = this.handleDecimalClick.bind(this);
        this.handleClearClick = this.handleClearClick.bind(this);
        this.handleEqualsClick = this.handleEqualsClick.bind(this);
    }

    handleNumberClick(e) {
        const operators = ["+", "-", "*", "/"];
        const lastChar = this.state.formula.slice(-1);
        if (this.state.evaluated  ) {
            this.setState({
                displayValue: e.target.value,
                formula: e.target.value !== "0" ? e.target.value : "",
                clearFormula: e.target.value !== "0" ? e.target.value : "",
                evaluated: false,
            });
        } else {
            if (this.state.displayValue === "0") {
                this.setState({
                    displayValue: e.target.value,
                    formula: e.target.value !== "0" ? e.target.value : "",
                    clearFormula: e.target.value !== "0" ? e.target.value : "",
                });
            } else {
                if(operators.includes(lastChar)) {
                    this.setState((prevState) => ({
                        displayValue:  e.target.value,
                        formula: prevState.formula + e.target.value,
                        clearFormula: prevState.clearFormula + e.target.value,
                    }));
                } else {
                this.setState((prevState) => ({
                    displayValue: prevState.displayValue + e.target.value,
                    formula: prevState.formula + e.target.value,
                    clearFormula: prevState.clearFormula + e.target.value,
                }));
                }
            }
        }
    }

    handleOperatorClick(e) {
        const operators = ["+", "-", "*", "/"];
        const lastChar = this.state.formula.slice(-1);
        if (!operators.includes(lastChar) && this.state.formula !== "") {
            this.setState((prevState) => ({
                displayValue: e.target.value,

                    formula: prevState.clearFormula === this.state.formula ? prevState.formula + e.target.value : prevState.clearFormula + e.target.value,
                clearFormula: prevState.clearFormula === this.state.formula ? prevState.formula + e.target.value : prevState.clearFormula + e.target.value,

                evaluated: false,
            }));
        } else if (e.target.value === "-" && this.state.formula.length === 0) {
            this.setState({
                displayValue: e.target.value,
                formula: e.target.value,
                clearFormula: e.target.value,
                evaluated: false,
            });
        } else if (lastChar === "-" && this.state.formula.length === 1) {
            this.setState({
                displayValue: e.target.value,
                formula: e.target.value,
                clearFormula: e.target.value,
                evaluated: false,
            });
        }
        else if (operators.includes(lastChar) && e.target.value==="-" && this.state.formula.length > 1) {
            this.setState((prevState) => ({
                displayValue: e.target.value,
                formula: prevState.formula + e.target.value,
                clearFormula: prevState.formula+ e.target.value,
                evaluated: false,
            }));
        }else if (lastChar !== "-" && this.state.formula.length > 1) {
            this.setState((prevState) => ({
                displayValue: e.target.value,
                formula: prevState.formula.slice(0, -1) + e.target.value,
                clearFormula: prevState.formula.slice(0, -1) + e.target.value,
                evaluated: false,
            }));
        }else if (lastChar === "-" && operators.includes(e.target.value)) {
            this.setState((prevState) => ({
                displayValue: e.target.value,
                formula: prevState.formula.slice(0, -2) + e.target.value,
                clearFormula: prevState.formula.slice(0, -2) + e.target.value,
                evaluated: false,
            }));
        }
    }

    handleDecimalClick() {
        const lastNum = this.state.displayValue.slice(-1);
        if (!this.state.displayValue.includes(".") && !isNaN(lastNum)) {
            this.setState((prevState) => ({
                displayValue: prevState.displayValue + ".",
                formula: prevState.formula + ".",
                clearFormula: prevState.formula + "."
            }));
        }
    }

    handleClearClick() {
        this.setState({
            displayValue: "0",
            formula: "",
            clearFormula: "",
            evaluated: false,
        });
    }

    handleEqualsClick() {
        const result = eval(this.state.formula);
        this.setState({
            displayValue: result.toString(),
            clearFormula: result.toString(),
            formula: this.state.formula + "=" + result.toString(),
            evaluated: true,
        });
    }

    render() {
        return (
            <div className={classes.app}>
                <div>
                <div id="calculator" className={classes.calculator} >
                    <div className={classes.formulaScreen} id="formulaScreen">{this.state.formula}</div>

                    <div className={classes.outputScreen} id="display">{this.state.displayValue}</div>

                    <button id="clear" onClick={this.handleClearClick} className={classes.jumbo} >
                        AC
                    </button>
                    <button id="divide" value="/" onClick={this.handleOperatorClick} className={classes.operator}>
                        /
                    </button>
                    <button id="multiply" value="*" onClick={this.handleOperatorClick} className={classes.operator}>
                        x
                    </button>
                    <button id="seven" value="7" onClick={this.handleNumberClick}>
                        7
                    </button>
                    <button id="eight" value="8" onClick={this.handleNumberClick}>
                        8
                    </button>
                    <button id="nine" value="9" onClick={this.handleNumberClick}>
                        9
                    </button>
                    <button id="subtract" value="-" onClick={this.handleOperatorClick} className={classes.operator}>
                        -
                    </button>
                    <button id="four" value="4" onClick={this.handleNumberClick}>
                        4
                    </button>
                    <button id="five" value="5" onClick={this.handleNumberClick}>
                        5
                    </button>
                    <button id="six" value="6" onClick={this.handleNumberClick}>
                        6
                    </button>
                    <button id="add" value="+" onClick={this.handleOperatorClick} className={classes.operator}>
                        +
                    </button>
                    <button id="one" value="1" onClick={this.handleNumberClick}>
                        1
                    </button>
                    <button id="two" value="2" onClick={this.handleNumberClick}>
                        2
                    </button>
                    <button id="three" value="3" onClick={this.handleNumberClick}>
                        3
                    </button>
                    <button id="decimal" onClick={this.handleDecimalClick} className={classes.operator}>
                        .
                    </button>

                    <button id="zero" value="0" onClick={this.handleNumberClick} className={classes.zero}>
                        0
                    </button>
                    <button id="equals" onClick={this.handleEqualsClick} className={classes.equals}>
                        =
                    </button>


                </div>
                <div className={classes.author}> Coded By <a href="" target="_blank">Ruslana Chaika</a></div>
                </div>
            </div>
        );
    }
}
export default Calculator;
